@charset "utf-8";
.flex {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}
.l_flex {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  -ms-flex: 1;
  flex: 1;
}
html {
  font-size: 625%;
}
html,
body {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
body {
  font-size: .16rem;
  color: #85f4fb;
}
@font-face {
  font-family: 'digital';
  src: url('../font/Digital2.ttf');
}
html,
body,
div,
table,
ul,
li,
ol,
dl,
dd,
dt,
a,
img,
label,
p,
h1,
h2,
h3,
h4,
h5,
h6,
span {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
}
input {
  margin: 0;
  padding: 0;
}
ul,
ol {
  list-style: none;
}
em,
i {
  font-style: normal;
}
strong {
  font-weight: normal;
}
img {
  max-width: 100%;
  display: block;
}
textarea {
  resize: none;
}
form {
  margin: 0;
}
button {
  outline: none!important;
}
button:focus {
  outline: none!important;
}
a {
  color: #4f5f6f;
}
a,
a:hover,
a:focus {
  text-decoration: none;
  outline: none;
}
a:hover,
a:focus {
  color: #2286d3;
}
.wrap {
  width: 100%;
  margin: 0 auto;
}
.digital {
  font-family: 'digital';
}
.gradient {
  background: linear-gradient(rgba(62, 155, 221, 0.7) 40%, rgba(255, 255, 255, 0));
}
.txtcol-green {
  color: #42a651;
}
.txtcol-red {
  color: #f02020;
}
.ec_a {
  display: inline-block;
  margin-right: 1vw;
  height: 3vh;
  line-height: 3vh;
  background: url(../images/ecotope_jiao.png);
  background-size: 100% 100%;
  text-align: center;
}
.ec_a:last-child {
  margin-right: 0;
}
.ec_a span {
  display: block;
  width: 100%;
  font-size: .15rem;
  padding: 0 .15rem;
}
.ec_a.active span {
  background: linear-gradient(rgba(62, 155, 221, 0.7) 40%, rgba(255, 255, 255, 0));
}
.footnav {
  position: absolute;
  bottom: 0;
  width: 100%;
  z-index: 2;
  height: 1rem;
  background: url(../images/foot_nav_bg.png) no-repeat;
  background-size: cover;
  padding-top: 1px;
  box-sizing: border-box;
  text-align: center;
}
.footnav ul {
  display: inline-block;
  margin-top: .2rem;
}
.footnav ul li {
  float: left;
  text-align: center;
  margin: 0 .1rem;
  border-radius: 5px;
}
.footnav ul li i {
  display: block;
  margin: 0 auto;
}
.footnav ul li a {
  display: inline-block;
  width: 100%;
  height: 100%;
  padding: 2px .1rem;
  box-sizing: border-box;
  color: #fff;
}
.footnav ul li a:hover,
.footnav ul li a:active {
  color: #fff;
}
.footnav ul li:hover,
.footnav ul li.active {
  background: rgba(0, 0, 0, 0.3);
  box-shadow: 0 0 16px rgba(0, 0, 0, 0.3) inset;
}
.newheader {
  width: 100%;
  height: 7vh;
  background: url(../images/head.png) no-repeat;
  background-size: 100% 100%;
  position: absolute;
  top: 0;
  z-index: 9;
}
.newheader.money {
  background: url(../images/head_money.png) no-repeat;
  background-size: 100% 100%;
}
.newheader.ecotope {
  background: url(../images/head_ecotope.png) no-repeat;
  background-size: 100% 100%;
}
.newheader.infrastructure {
  background: url(../images/head_Infrastructure.png) no-repeat;
  background-size: 100% 100%;
}
.newheader .head-time,
.newheader .head-weather {
  line-height: 7vh;
}
.newheader .head-time {
  font-family: 'digital';
  font-size: .26rem;
  margin-left: 0.22rem;
  color: #85f4fb;
}
.newheader .head-time > span.ht-month {
  letter-spacing: 4px;
  margin-left: .08rem;
  margin-right: .07rem;
}
.newheader .head-time > span.ht-week {
  font-size: .22rem;
  letter-spacing: 2px;
  font-weight: bold;
  font-style: italic;
}
.newheader .head-weather > a {
  font-size: 0.18rem;
  color: white;
  font-weight: bold;
  margin-right: .1rem;
}
.newheader .head-weather i {
  vertical-align: middle;
}
.jiao {
  position: relative;
}
.jiao:before,
.jiao:after {
  content: '';
  display: inline-block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  background: url(../images/list_jiao1.png) no-repeat -1px -1px, url(../images/list_jiao2.png) no-repeat right -1px;
}
.jiao:after {
  bottom: 0;
  background: url(../images/list_jiao3.png) no-repeat -1px bottom, url(../images/list_jiao.png) no-repeat right bottom;
}
.jiao_small {
  position: relative;
}
.jiao_small:before,
.jiao_small:after {
  content: '';
  display: inline-block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  background: url(../images/list_jiao1_small.png) no-repeat -1px -1px, url(../images/list_jiao2_small.png) no-repeat right -1px;
}
.jiao_small:after {
  bottom: 0;
  background: url(../images/list_jiao3_small.png) no-repeat -1px bottom, url(../images/list_jiao_small.png) no-repeat right bottom;
}
.cont-box {
  border: 1px solid #59aceb;
  background-color: rgba(5, 35, 110, 0.92156863);
  overflow: hidden;
  color: #85f4fb;
  margin-bottom: 0.1rem;
}
.cont-box a {
  color: #85f4fb;
}
.cont-box .cont-box-kid {
  position: relative;
  z-index: 2;
  padding: 0 .2rem;
  text-align: center;
}
.cont-box .title {
  width: 3.4rem;
  height: .35rem;
  background: url(../images/head_s.png);
  background-size: 100% 100%;
  margin: -1px auto 2vh auto;
  font-size: .2rem;
  color: #dcfdff;
  text-align: center;
  line-height: .35rem;
}
.cont-box .cont-box-all {
  width: 100%;
  position: relative;
  z-index: 2;
}
.cont-box .cont-box-all .cont-title {
  background-size: 100% 100%;
  width: 3.4rem;
  height: .36rem;
  line-height: .36rem;
  font-size: 0.22rem;
  color: #dcfdff;
  text-align: center;
  margin: -1px auto;
  background: url("../images/head_s.png") no-repeat;
}
.cont-center {
  margin-top: .1rem;
}
.cont-center .cont-title {
  width: 2.7rem;
  margin: 0 auto;
}
.cont-center .cont-title span {
  display: block;
  height: .5rem;
  text-align: center;
  line-height: .5rem;
  color: #fff;
  font-size: .2rem;
}
.cont-center .cont-list {
  text-align: center;
}
.cont-center .cont-list ul {
  margin-top: .1rem;
  display: inline-block;
}
.cont-center .cont-list ul li {
  float: left;
  border-radius: .1rem;
  margin-right: .1rem;
  box-sizing: border-box;
  padding: .1rem 0;
}
.cont-center .cont-list ul li:last-child {
  margin-right: 0;
}
.cont-center .cont-list ul li span {
  font-size: .26rem;
  font-weight: bold;
  color: #fff;
}
.cont-center .cont-list ul li p {
  margin: .1rem auto 0 auto;
  width: 1.4rem;
  height: .5rem;
  line-height: .6rem;
  font-size: .44rem;
  border-radius: .2rem;
  background: rgba(41, 111, 221, 0.9);
  text-align: center;
}
.cont-center .cont-list ul li:nth-child(1) {
  background: url(../images/cont-list1.png);
  background-size: 100% 100%;
}
.cont-center .cont-list ul li:nth-child(2) {
  background: url(../images/cont-list2.png);
  background-size: 100% 100%;
}
.cont-center .cont-list ul li:nth-child(3) {
  background: url(../images/cont-list3.png);
  background-size: 100% 100%;
}
.contain {
  padding: .9rem .1rem;
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: url(../images/bg.png) no-repeat;
  background-size: 100% 100%;
}
